import React from 'react';
import quizQuestions from './quizQuestions.js';
import Quiz from './Quiz.js';
import Result from './Result.js';

export class AppQuestion extends React.Component {
    constructor(props) {
        super(props);
      
        this.state = {
          counter: 0,
          questionId: 1,
          question: '',
          answerOptions: [],
          answer: '',
          answersCount: {},
          result: '',
          desc: '',
          statut: '',
          famous: '',
        };
        this.handleAnswerSelected = this.handleAnswerSelected.bind(this);
      }

      componentDidMount() {
        const shuffledAnswerOptions = quizQuestions.map((question) => this.shuffleArray(question.answers));  
      
        this.setState({
          question: quizQuestions[0].question,
          answerOptions: shuffledAnswerOptions[0]
        });
      }

      shuffleArray(array) {
        var currentIndex = array.length, temporaryValue, randomIndex;
      
        while (0 !== currentIndex) {
          randomIndex = Math.floor(Math.random() * currentIndex);
          currentIndex -= 1;
          temporaryValue = array[currentIndex];
          array[currentIndex] = array[randomIndex];
          array[randomIndex] = temporaryValue;
        }
      
        return array;
      };

      setUserAnswer(answer) {
        this.setState((state) => ({
          answersCount: {
            ...state.answersCount,
            [answer]: (state.answersCount[answer] || 0) + 1
          },
          answer: answer
        }));
      }

      setNextQuestion() {
        const counter = this.state.counter + 1;
        const questionId = this.state.questionId + 1;
        this.setState({
          counter: counter,
          questionId: questionId,
          question: quizQuestions[counter].question,
          answerOptions: quizQuestions[counter].answers,
          answer: ''
        });
      }

      handleAnswerSelected(event) {
        this.setUserAnswer(event.currentTarget.value);
        if (this.state.questionId < quizQuestions.length) {
            setTimeout(() => this.setNextQuestion(), 300);
          } else {
            setTimeout(() => this.setResults(this.getResults()), 300);
          }
      }

      getResults() {
        const answersCount = this.state.answersCount;
        const answersCountKeys = Object.keys(answersCount);
        const answersCountValues = answersCountKeys.map((key) => answersCount[key]);
        const maxAnswerCount = Math.max.apply(null, answersCountValues);
      
        return answersCountKeys.filter((key) => answersCount[key] === maxAnswerCount);
      }

      setResults (result) {
        if (result.length === 1) {
          this.setState({ result: result[0] });
          switch(true){
            case (this.state.result == "une grosse guidoune"):
                this.setState({ desc: "comme ton nom le laisse penser, tu es en bas de l'chelle alimentaire, succombant  la moindre dose de force et de bon sens s'opposant  toi. Tu utilises tes supposs talents pour clairer ta vie pathtique, et tu penses que tu n'as aucun dfaut, qu'on peut pourtant compter par centaines, comme tes chromosomes. Les gens te seront cependant reconnaissants de ne pas gcher leur bon oxygne  dblatrer des idioties  longueur de journe. Gros coeur sur toi, mme si ton cas est des plus dsesprs.", 
                                statut:'Guidounes' , 
                                famous: "Justin Bieber, Kim Kardashian, Emmanuel Macron, ta maman, Nicki Minaj, Florentin"});
                break;

            case (this.state.result == "un jeune bdouin"):
                this.setState({ desc: "il semblerait que tes mthodes exotiques  et ta relation avec la nature soient endmiques  une lointaine contre, probablement isole de toute civilisation moderne. Tu es un concentr d'inattendu et tu peux facilement faire peur aux chiens. Les us et coutumes qubecoises restent encore  acqurir, mais tu restes trop attach  tes racines tropicales pour t'en dfaire, qu'on ne manquera jamais de te faire remarquer.", 
                                statut:'Bdouins' , 
                                famous: "le xnomorphe, le Dala-Lama, le chef Raoni, Nicolas Hulot, les Australiens, Bouddha"});
                break;
                
            case (this.state.result == "un chasseur de caribou"):
                this.setState({ desc: "tu penses que le monde est une gigantesque rserve naturelle de guidounes, que tu dcimes pour ne pas choper de maladie. Tu te parfumes avec ta propre sueur, chasse ta propre nourriture  l'aide de tes muscles, et ne te baigne jamais, si ce n'est dans ton propre jus. Tu suintes la virilit, et ta seule prsence fait pousser de la barbe mme  ces guidounes d'enfants, clisses d'imberbes. Tu es l'exact oppos du consul montralais.", 
                                statut:'Chasseurs' , 
                                famous: "Sylvester Stallone, Vladimir Poutine, Chuck Norris, Garou, ta grand-mre, Gordon Ramsay"});
                break;
                
            case (this.state.result == "un matre-poutinier"):
                this.setState({ desc: "tu as le don de faire plaisir  tous ceux qui t'entourent, sans modration, mais galement pour ton propre plaisir, gagant ainsi le respect de toute la communaut. Tu es un vrai bon-vivant, et un mauvais-mort, mme si a ne se dit pas, mais bon, on s'est compris, c'est le principal. Tu ne recherches jamais la reconnaissance, le bonheur de tes proches tant tout ce qui te vaille. Tu es l'exact oppos de la grosse guidoune", 
                                statut:'Matres-poutiniers' , 
                                famous: "Mat, Francis Lalanne, Jol Robuchon, Patrick Sbastien, Keanu Reeves, les mecs de chez Nachos"});
                break;
                
            case (this.state.result == "un astrophysicien lgendaire"):
                this.setState({ desc: "la sagesse est ton crdo, l'exprience ton arme de prdilection. Grce  ton ge probablement canonique, tu es en mesure de connatre la vraie valeur des choses et de modrer tes actions afin de laisser la vie faire son travail. Tu te ferais mme un petit plaisir  tuer tes adversaires  petit feu, avec leurs propres erreurs. Tu es la plus intangible et honorable des guidounes.", 
                                statut:'Astrophysiciens' , 
                                famous: "Justin Bieber, Avril Lavigne, Emmanuel Macron"});
                break;
                
            case (this.state.result == "un consul montralais"):
                this.setState({ desc: "tu parles comme un diplomate, mais tu frappes comme un diplomate. Ton don se rsume  rgler les situations de la plus calme des manires, la violence ne faisant pas partie de tes cordes. Tu penses que c'est en unissant le monde qu'il prosprera dans la plus parfaite harmonie, tandis que tu seras toujours aussi bien sap. Tu es l'exact oppos du chasseur de caribou.", 
                                statut:'Consuls' , 
                                famous: "Mre Thrsa, Nelson Mandela, le pape Franois, Jacques Cartier, ton thrapeute matrimonial, Marc-Emmanuel"});
                break;

            default :
                this.setState({ result: 'un mystre de la nature',
                                desc: "on ne sait pas trop ce que tu fais ici, c\'est la catgorie un peu fourre-tout, dsol.",
                                statut:'Mystres',
                                famous: 'les Miss France, la plupart des chanteurs modernes, le Yeti, mon voisin dans le placard de ma chambre, ton code gntique, un fromage dans le rayon des lessives'});
        }
        } else {
        this.setState({ result: 'un mystre de la nature',
                        desc: "on ne sait pas trop ce que tu fais ici, c\'est la catgorie un peu fourre-tout, dsol.",
                        statut:'Mystres',
                        famous: 'les Miss France, la plupart des chanteurs modernes, le Yeti, mon voisin dans le placard de ma chambre, ton code gntique, un fromage dans le rayon des lessives' });
        }
      }

      renderQuiz() {
        return (
          <Quiz
            answer={this.state.answer}
            answerOptions={this.state.answerOptions}
            questionId={this.state.questionId}
            question={this.state.question}
            questionTotal={quizQuestions.length}
            onAnswerSelected={this.handleAnswerSelected}
          />
        );
      }
      
      renderResult() {
        return (
          <Result quizResult={this.state.result} desc={this.state.desc} famous={this.state.famous} statut={this.state.statut} />
        );
      }

  render() {
    return (
    <React.Fragment>
        <div class="journalContainer">     
        <div class="journal">
        <div className = "quizHeader">
            <h2 class="blog-title">Quel archtype es-tu ?</h2>
            </div>  
        <p class="lead blog-description">Un questionnaire des plus rigolos</p>
        {this.state.result ? this.renderResult() : this.renderQuiz()}
        </div>
        </div>
    </React.Fragment>
      );
  }
}

export default AppQuestion;